.App {
  text-align: center;
  font-family: 'Rubik', sans-serif;
}

body {
  background: rgb(194, 213, 154);
}

hr {
  border-color: brown;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

header, footer {
  padding-left: 2px;
  padding-right: 2px;
  margin-top: 2px;
  margin-bottom: 2px;
}

#name {
  font-size: 2em;
}

#titles{
  font-size: 1.5em;
}

.my-logo {
  max-height: 80px;
  padding-top: 5px;
  border-radius: 60%/50%;
}

.fa-github, .fa-envelope, .fa-linkedin {
  margin-left: 55px;
  margin-right: 2px;
}

.my-photo {
  border-radius: 50%;
  height: 110px;
  margin-bottom: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#dialogue {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.me {
  padding: 5px;
  border-radius: 0px 17px 10px 10px;
  /* background: rgb(216, 242, 242); */
  /* margin-right: auto; */
  background: rgb(154, 208, 242);
  margin-left: 20px;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.user {
  border-radius: 17px 0px 10px 10px;
  /* background: rgb(189, 227, 179); */
  background: rgb(129, 145, 228);
  padding: 5px;
  text-align: right;
  margin-left: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.text-container {
  width: 100%;
}

.project-container {
  border: 8px ridge rgba(61, 65, 61, 0.7);
  border-radius: 30px/10px;
  background: rgb(250, 217, 205);
  margin-bottom: 10px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
}

.project-container > p {
  text-align: left;
  padding-left: 10px;
}

.project-container > p > em {
  font-weight: bold;
}

.dialogue-button {
  padding: 5px;
  font-size: 1.5em;
  margin-left: 4px;
  margin-right: 4px;
  border-width: thick;
  font-weight: bold;
  background: black;
  border-radius: 10px;
  cursor: pointer;
}

#yes {
  color: rgb(51, 221, 0);
}

#no {
  color: red
}

#user-options {
  margin-top: 20px;
  margin-bottom: 30px;
}

.skip-button {
  color: white;
  background: black;
  padding: 2px;
  margin-left: 200px;
  margin-left: 10px;
  border-width: thick;
  border-radius: 10px;
  cursor: pointer;
}

footer {
  margin-top: 50px;
  margin-bottom: 5px;
}

.skillList {
  text-align: left;
}

.project-thumb {
  max-width: 100%;
}

.avatar {
  width: 25px;
  height: 25px;
  float: left;
  border-radius: 50%;
}

.author-message {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#replying {
  border-radius: 10px;
  background: rgb(154, 208, 242);
  margin-left: 20px;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 2em;
  position: relative;
  padding-bottom: 40px;
  padding-left: 19px;
  padding-right: 19px;
}

.bounceFirst {
  left: 2px;
  position: absolute;
  -webkit-animation: bounce 0.8s infinite;
          animation: bounce 0.8s infinite;
}

.bounceSecond {
  position: absolute;
  left: 10px;
  -webkit-animation: bounce 0.8s infinite;
          animation: bounce 0.8s infinite;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.bounceThird {
  position: absolute;
  left: 20px;
  -webkit-animation: bounce 0.8s infinite;
          animation: bounce 0.8s infinite;
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

@-webkit-keyframes bounce {
  0%
  {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50%
  {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  100%
  {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes bounce {
  0%
  {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50%
  {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  100%
  {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.icon {
  color: black;
}

.icon-media {
  color: black;
  font-size: 2.5em;
}

.mail {
  display: block;
  margin-top: 10px;
  margin-right: 30px;
}

.linkedin {
  margin-right: 80px;
}

#media-container {
  margin-left: auto;
  margin-right: auto;
  padding-right: 40px;
}

@media screen and (min-width: 400px) {
  .mail {
    font-size: 1.3em;
  }
}

@media screen and (min-width: 600px) {
  #dialogue {
    width: 70%;
  }
}

@media screen and (min-width: 750px) {
  #dialogue {
    width: 60%;
  }
  #name {
    font-size: 2.3em;
  }
  #titles{
    font-size: 1.8em;
  }
  h3 {
    font-size: 1.3em;
  }
  #all-projects {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  #all-projects > .project-container {
    width: 48%;
  }
}

@media screen and (min-width: 900px) {
  #all-projects {
    width: 85%;
  }
  #all-projects > .project-container {
    width: 31%;
  }
}

@media screen and (min-width: 1000px) {
  #dialogue {
    width: 50%;
  }
  header {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    display: -ms-flexbox;
    display: flex;
  }
  .my-logo {
    width: 80px;
    margin-top: 40px;
    margin-right: 10%;
  }
  .header-container {
    float: right;
    /* padding-left: 50px; */
    width: calc(50%-100px);
  }
}

@media screen and (min-width: 1200px) {
  .App {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
}



